 introduction poll will exist prior to creating \"community event\" in order to gather relevant data from\n          Nextdoor users and smoothly introduce the idea of \"community event\".\n        ")]),_c('img',{staticClass:"flow-2",attrs:{"src":_vm.flow2Img,"alt":"flowchart/user-flow"}}),_c('p',{staticClass:"p caption"},[_vm._v("\n          Flow of user interaction with \"community event\"\n        ")]),_c('p',{staticClass:"p paddingTop paddingBottom large"},[_vm._v("\n          If the \"community event\" is in high demand, Nextdoor will create the special event page with suggested data.\n          Nextdoor should actively promote this \"community event\" through its notification system, as well as occupying\n          a space on the news feed page in order to emphasize the event. The event will have a poll that suggests\n          the neighbors to decide on the event itself and its location.\n        ")]),_c('h4',{staticClass:"h4 paddingTop paddingBottom"},[_vm._v("\n          Wireframing + Prototyping\n        ")]),_c('img',{staticClass:"wireframe paddingTop paddingBottom",attrs:{"src":_vm.wireframeImg,"alt":"wireframe"}}),_c('h4',{staticClass:"h4 paddingTop"},[_vm._v("\n          UI Design\n        ")]),_c('p',{staticClass:"p paddingTop paddingBottom"},[_vm._v("\n          I made sure to coincide with the consistency of Nextdoor's UI. Normally, a \"yes or no\"\n          poll would have this type of components:\n        ")]),_c('img',{staticClass:"yes-no",attrs:{"src":_vm.yesNoImg,"alt":"yes or no"}}),_c('p',{staticClass:"p caption paddingBottom"},[_vm._v("\n          Simple and common Yes or No selection option.\n        ")]),_c('p',{staticClass:"p center paddingTop paddingBottom small"},[_vm._v("\n          Nextdoor's checkmark UI component:\n        ")]),_c('img',{staticClass:"checkmark",attrs:{"src":_vm.checkmarkImg,"alt":"checkmark"}}),_c('p',{staticClass:"p caption paddingBottom"},[_vm._v("\n          Taken from Nextdoor's privacy settings page.\n        "